<template>
  <div class="title">修改协议</div>
  <input type="text" class="name" placeholder="输入协议名" v-model="value" />
  <div class="search" @click="confirm">搜索</div>
  <div class="search" @click="modify">确认</div>
  <Common :editable="true" :url="url"></Common>
</template>

<script>
import Common from "../../../../agreements/components/common.vue";
import config from "../../../../agreements/assets/common.config";
const arr = [];
for (const key in config) {
  arr.push({ url: key, ...config[key] });
}
import { ref } from "@vue/reactivity";
export default {
  name: "i-i2",
  setup() {
    const value = ref("");
    const url = ref("consumer");
    return {
      value,
      url,
    };
  },
  computed: {},
  methods: {
    confirm() {
      if (!this.value) return;
      const selected = arr.filter((r) => r.h1.includes(this.value));
      console.log(selected);
      if (selected.length) {
        this.url = selected[0].url;
      } else {
        this.url = "consumer";
      }
    },
    modify() {
      // post Common组件实例的数据
      console.log("success");
    },
  },
  components: {
    Common,
  },
};
</script>

