                "•如果您或您的监护人（如果您是未成年人）不同意本政策的任何内容，您可以使用营探的部分功能（包括搜索、浏览），但您将无法使用营探的产品和服务。如您对本政策内容有任何疑问、意见或建议，您可以将相关问题发送至service@camptogo.com，一般情况下我们在收到您的信息并核实您的身份后十五（15）日内回复。",
