                "b. 若您是授权用户，您需知悉用户在向您发送邀请时可能已经向营探提供了您的手机号，但当且仅当您确认接受邀请时，您在该组织下的帐号身份才会被激活；若您主动申请加入某个组织，在您的加入申请被通过时，您在该组织下的帐号身份即被创建，待您登录后该帐号身份则被激活。",
                "c. 当您代表用户（法人和其他组织）注册本服务时，您还需提供企业/组织名称、行业类型、人员规模等相关信息。若您希望获得更多功能与权益，需要您代表客户提供主体证照、法定代表人或其他类型管理者信息、授权函件等基本信息，以便我们帮助客户完成认证。您确认已获得这类客户的充分授权签署并同意本隐私政策的全部内容。",
                "d.基于我们与通信运营商的合作，当您使用“一键登录”功能时，经过您的明示同意，运营商会将您的手机号发送给我们，便于我们为您提供快捷的登录服务。手机号属于个人敏感信息，如果拒绝提供将无法使用“一键登录”方式注册登录本服务，但不影响您通过其他方式注册登录。",
