                "4、实现广告相关共享信息的共享行为",
                "a.我们可能与进行推广和广告投放的合作伙伴共享信息用于向您展示、推送信息商业广告，但我们不会共享可以识别您个人身份的信息，仅会向这些合作伙伴提供不能识别您个人身份的间接画像标签及去标识化的设备信息或匿名化后的设备、网络、渠道信息，以帮助这些合作伙伴在不识别您个人身份的前提下提升广告有效触达率。例如，我们可以告知该委托方在浏览了委托方推广信息或在看到这些信息后购买了委托方的商品的人数，或者向委托方提供不能识别个人身份的统计信息，以帮助其了解受众或顾客。",
                "b.如果您不想接受我们给您发送的商业广告/短信，您可通过短信提示回复退订或拨打我们的客服电话010-87511881进行退订或关闭。上述业务场景仅限于具有权限的业务人员和经您同意其他人员可以访问您的个人信息；此业务场景中将形成消除明确身份指向性的间接用户画像，用于了解您的偏好和使用习惯，进行数据分，不产生新的个人信息。",
                "5、实现安全与分析统计的共享行为",
                "a. 风险识别功能：为保障您和其他用户的帐号与财产安全，使您和我们的正当合法权益免受不法侵害，我们和关联方或服务提供商可能会共享必要的设备、帐号及日志信息。以预防、发现、调查欺诈危害安全、非法或违反与我们或其关联方协议、政策或规则的行为（包括但不限于账号活动异常、多端登录、流量异常），以保护您、其他用户，或我们或其关联方的合法权益。请您注意，基于业务合作伙伴和关联公司防范和检测欺诈的目的，我们可能也会审慎地提供您的必要个人信息，这些个人信息的提供会经过严格的内部安全评估和审批并通过签署相应的协议限定这些信息的使用目的。",
                "b. 安全保障功能：为确保我们提供给您的操作环境安全，可及时识别账号异常状态，保护您或其他用户或公众的人身财产安全免遭侵害，预防计算机病毒、钓鱼网站、网络攻击安全风险，我们会共享您的账户信息、设备信息、有关网络日志给我们的合作伙伴。用来综合判断您账户风险、进行身份验证、检测及防范安全事件，并依法采取必要的记录、审计、分析、处置措施。",
                "c.为遵守法律、执行或适用我们的使用条件和其他协议，或者为了保护营探、您或其他用户的权利及其财产或安全，比如为防止欺诈等违法活动和减少信用风险，而与其他公司和组织交换信息。不过，这并不包括违反本隐私政策中所作的承诺而为获利目的出售、出租、共享或以其它方式披露的个人信息。",
