                "（二）转移您的个人信息",
                "我们不会将您的个人信息转移给任何公司、组织和个人，但事先获得您的明确同意或授权；根据适用的法律法规、法律程序的要求、强制性的行政或司法要求；在涉及合并、分立、收购、资产转让或类似的交易时，如涉及到个人信息转移，我们会在正式进行信息转移前，向您告知接收信息公司、组织的名称和联系方式，并要求新的持有您个人信息的公司、组织继续受本政策的约束，继续履行原先由我们承担的个人信息处理义务。否则,我们将要求该公司、组织重新向您征求授权同意的上述三种情况除外。",
                "（三）委托处理您的个人信息",
                "为了提升信息处理效率，降低信息处理成本或提高信息处理准确性，我们可能会委托有专业技术能力的关联公司或营探以外的外部服务供应商处理您的个人信息。我们会与受托方签署委托处理协议，并要求受托方按照委托处理协议、本政策以及其他任何相关的保密和安全措施来处理个人信息。委托关系不生效、无效或被撤销或者终止时，我们会要求受托方返还、删除您的个人信息(包括但不限于因委托处理获取到的原始信息或副本、摘要等)，不得保留。未经您的授权同意，我们禁止受托方转委托他人处理您的个人信息。",
                "（四）公开披露您的个人信息",
                "我们不会公开披露您的信息，除非是根据法律、法规要求或者征得您的同意。我们公开披露您的个人信息会事先开展个人信息安全影响评估，并依评估结果采用符合行业内标准的安全保护措施。",
                "1、法律法规要求披露信息的处理",
                "我们根据法律法规、强制性的行政执法或司法要求所必须提供您个人信息的情况下，我们会要求上述单位必须出具与之相应的法律文件，如传票或调查函作为依据，并明确其要求的个人信息类型和披露方式。我们对所有的请求都进行慎重的审查，以确保其具备合法依据，且仅限于执法部门因特定调查目的且有合法权利获取的数据。在法律法规许可的前提下，我们将尽量以加密方式提供您的个人信息。",
                "2、您个人在营探主动公开的信息",
                "我们会公开您在营探主动公开的信息，用于帮助您实现评论功能、分享功能。这些信息包括您的个人信息、服务项目及其他公开信息。我们公布上述公开信息的场景包括：向其他人公开您的公开个人资料页面中的部分内容（姓名、账号、头像、您的描述和城市）；向公众开放公开服务项目页面，包括您预订的产品/服务大致地理位置（所处的街区和城市）或准确位置（经您同意）、产品/服务项目描述、产品/服务日期、向服务提供者公开个人头像、汇总的需求信息（如某段时间内查看页面的次数）及您选择分享的任何其他信息；您在营探社区、论坛、博客或社交媒体发布内容，或使用营探平台上的类似功能，相关内容会向公众开放；根据推广营探的合法利益，我们可能会在营探商业合作伙伴运营的网站上通过使用小工具或API技术显示部分营探内容，如果您的服务项目显示在合作伙伴的网站上，您的公开个人资料页面中的信息也可能会显示；对于您参加营销促销活动，公布中奖名单时您的个人信息可能会经脱敏后公布，包括您的手机号、姓名、账号、所在城市等信息。",
