                "2.重大事件（Ⅱ级）",
                "发生有害程序事件、网络攻击事件、设备设施故障、灾害性事件造成营探大面积网络与信息系统瘫痪，或发生信息内容安全事件和信息破坏事件，对营探正常工作造成严重损害，事态发展超出营探技术部门控制能力，需要营探及相关网络安全专业厂商协同处置的安全事件。",
                "3.较大事件（Ⅲ级）",
