<template>
  <div id="agreements">
    <div class="protocals" v-show="showPro">
      <div class="protocal-items">
        <h1
          class="pagetitle"
          align="center"
          :contenteditable="editable"
          @blur="change"
        >
          {{ special.h1 }}
        </h1>
        <p id="updateTime" :contenteditable="false">{{ special.p(version) }}</p>
        <div class="agrees" v-for="(item, index) in protocals" :key="index">
          <h2
            class="h2title"
            :contenteditable="editable"
            @blur="(e) => changeTitle(e, index)"
          >
            {{ item.title }}
          </h2>
          <div class="itemps" v-for="(ite, inde) in item.detail" :key="inde">
            <p
              class="para"
              :contenteditable="editable"
              @blur="(e) => changeDetail(e, index, inde)"
            >
              {{ ite }}
            </p>
          </div>
          <br /><br /><br />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import commonData from "../assets/common.config";
export default {
  name: "test-1",
  props: ["editable", "url"],
  data() {
    return {
      version: "",
      protocals: [],
      showPro: true,
      special: { p: () => {} },
      // ps
    };
  },
  created() {},
  components: {},
  methods: {
    change(e) {
      // console.log(e,'change');
      this.special.h1 = e.target.innerText;
    },
    changeTitle(e, index) {
      // console.log(e,'change');
      console.log(this.protocals[index]);
      this.protocals[index].title = e.target.innerText;
      console.log(this.protocals[index]);
    },
    changeDetail(e, index, inde) {
      // console.log(e,chnage);
      console.log(this.protocals[index]);
      this.protocals[index].detail[inde] = e.target.innerText;
      console.log(this.protocals[index]);
    },
    showProtocals() {
      this.showPro = true;
      // this.axios.get("/api/Desktop/front/Peterpan/peterpan/src/assets/protocals.json").then(result => {
      // console.log(result);
      // }).catch(err => {
      //     console.log(err)
      // })
      const key = this.editable ? this.url : this.$route.params.name;
      const { data: result, h1, p } = commonData[key];
      this.version = result.version;
      this.protocals = result.agreement;
      this.special = { h1, p };
    },
  },
  watch: {
    url() {
      this.showProtocals();
    },
  },
  mounted() {
    window.addEventListener("load", this.showProtocals());
  },
};
</script>
